* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
}

body {
  max-width: 1024px;
  margin: 0 auto;
}
body #header {
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  background-color: #182127;
  margin: 0 auto;
  padding: 25px 0;
}
body #header .logo {
  background-image: url("../images/logo.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 220px;
  height: 150px;
  margin-left: 15px;
}
body #header .navigation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
}
body #header .navigation a {
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: #fedb0b;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 1px;
  padding: 3px;
  text-align: center;
}
body #header .navigation a::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0%;
  width: 0;
  height: 100%;
  border-radius: 5px;
}
body #header .navigation a:hover::after {
  animation: animate_menu 0.3s ease-in-out both;
}
body #header .date_and_time {
  display: flex;
  flex-direction: row;
  gap: 5px;
  margin-right: 15px;
}
body #header .date_and_time .date {
  font-size: 0.8rem;
  color: #fedb0b;
}
body #header .date_and_time .time {
  font-size: 0.8rem;
  color: #fedb0b;
}
@keyframes animate_menu {
  100% {
    width: 100%;
    border-bottom: 1px solid #fedb0b;
    border-top: 1px solid #fedb0b;
  }
}
body #about_me {
  display: flex;
}
body #about_me .image {
  flex-basis: 100%;
}
body #about_me .image img {
  width: 100%;
}
body #about_me .about_me_text {
  flex-basis: 100%;
}
body #about_me .about_me_text p {
  padding: 10px;
}
body .share_btn {
  background-color: #8787e4;
  color: #fff;
  transition: 0.2s ease-in-out;
  width: 100%;
  cursor: pointer;
  border: none;
  width: 100%;
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 900;
  padding: 10px 0;
}
body .share_btn:hover {
  transform: scale(1.05);
  box-shadow: 5px 0 10px #8787e4;
  border-radius: 5px;
}
body #welcome {
  display: flex;
  justify-content: space-between;
  background-color: aliceblue;
}
body #welcome form {
  display: flex;
  flex-direction: column;
  width: 30%;
  gap: 5px;
  margin: 15px 0 0;
  padding: 12px 8px;
}
body #welcome form li {
  list-style: none;
  display: flex;
  justify-content: space-between;
}
body #welcome form #form_submit_btn {
  padding: 12px 8px;
  border-radius: 5px;
  border: 2px solid #ccc;
  outline: #0063aa;
  margin-top: 15px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}
body #text {
  display: flex;
  width: 50%;
  background-color: #d5f2db;
  color: #606060;
  padding: 10px;
  line-height: 1.4;
  font-size: 1.2rem;
  border-radius: 5px 5px 0 0;
  margin-top: 10px;
  box-shadow: 0 -10px 10px #a6ffd3;
}
body #footer {
  display: flex;
  height: 20vh;
  background-color: #182127;
}